import { Component } from 'react';
import PropTypes from 'prop-types';

import MarvelService from '../../services/MarvelService';
import Spinner from '../spinner/Spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';
import Skeleton from '../skeleton/Skeleton';

import './charInfo.scss';

class CharInfo extends Component {

    state = {
        char: null,
        loading: false,
        error: false
    }

    
    marvelService = new MarvelService();

    componentDidMount() {
        this.updateChar();
    }

    componentDidUpdate(prevProps) {
        if(this.props.charId !== prevProps.charId) {
            this.updateChar();
        }
    }

    onCharLoaded = (char) => {
        this.setState({
            char, 
            loading: false
        })
    }

    onError = () => {
        this.setState({ 
            loading: false,
            error: true
        })
    }


    updateChar = () => {
        const {charId} = this.props;

        if(!charId) return;

        this.marvelService
            .getCharecter(charId)
            .then(this.onCharLoaded)
            .catch(this.onError)

        this.onCharLoading();
    }

    onCharLoading = () => {
        this.setState({
            loading: true
        })
    }


    render () {
        const {char, loading, error} = this.state;

        const skeleton = char || loading || error ? null :  <Skeleton/>; 
        const errorMessage = error ? <ErrorMessage/> : null;
        const spinner = loading ? <Spinner/> : null;
        const content = !(loading || error || !char) ? <View char={char}/> : null;

        return (
            <div className="char__info">
                {skeleton} 
                {errorMessage}
                {spinner}
                {content}
            </div>
        )
    }
}

const View = ({char}) => {

    const {name, description, thumbnale, homepage, wiki, comics} = char;

    let styles;
    if(thumbnale === 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
        styles = {objectFit: 'contain'};
    }

    let haveComics;

    if (comics.length === 0) {
        haveComics = 'There is no comics for this character'
    } else {
        haveComics = comics.map((item, i) => {
            return (
                <li key={i} className="char__comics-item">
                    {item.name}
                </li>
            )
        })
    }

    return (
        <>
            <div className="char__basics">
                <img src={thumbnale} alt="abyss" style={styles}/>
                <div>
                    <div className="char__info-name">{name}</div>
                    <div className="char__btns">
                        <a href={homepage} className="button button__main">
                            <div className="inner">homepage</div>
                        </a>
                        <a href={wiki} className="button button__secondary">
                            <div className="inner">Wiki</div>
                        </a>
                    </div>
                </div>
            </div>
            <div className="char__descr">
                {description}
            </div>
            <div className="char__comics">Comics:</div>
            <ul className="char__comics-list">
                {haveComics}
            </ul>
        </>
    )
}

CharInfo.propTypes = {
    charId: PropTypes.number 
}

export default CharInfo;